@import url('http://fonts.cdnfonts.com/css/adobe-clean');

:root{
  --yoo-primary:#D62486;
  --sys-secondary:#3F228C;
  --sys-dark:#101929;
  --sys-light:#ccc;
  --be-accent-default: #D62486;
}

.sys-primary{
  color:var(--yoo-primary);
}

.sys-primary-bg{
  background: var(--sys-dark);
}

.sys-primary-bg1{
  background: var(--yoo-primary) !important;
  color:white !important;
}
.sys-secondary-bg1{
  background:var(--sys-secondary) !important;
  color:white !important;
}
.sys-dark-bg1{
  background: var(--sys-dark) !important;
  color:white !important;
}
.sys-primary-mix{
  background: linear-gradient(93deg, #D62486, #3F228C);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}

.sys-dark-bg{
  background: var(--sys-dark);
}
.sys-primary-bg{
  color:var(--sys-secondary);
}

.sys-secondary{
  color:#3F228C;
}

.sys-secondary-bg{
  color:#3F228C;
}
body{
  margin: 0;
