.bigText {
  font-family: 'Adobe Clean', sans-serif;
  font-size: 4.2em;
  color: white;
  font-weight: 900;
  height: 75vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: absolute;
}
.home-padding{
  padding: 30px 55px;
}
#yoopageicon::after{
  content: '';
  position: absolute;
  background-image: url('/assets/pics/line.svg');
  background-size: 282px;
  width: 290px;
  background-repeat: no-repeat;
  height: 182px;
  top: 387px;
  left:302px;
}
.box-image {
  margin-top: 20px;
  width: 100%;
}
.flex-column-center
{
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 116px  0 0;
  //font-family: Segoe UI,Frutiger,Frutiger Linotype,Dejavu Sans,Helvetica Neue,Arial,sans-serif;
}

.flex-column-center h1{
  font-weight: 700;
  font-size: 3vw;;
}
.flex-column-center p{
  font-size: 19px;
}
/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .flex-column-center{
    padding: 0 63px 0 0;
  }
}
@media only screen and (max-width: 528px) {
  #yoopageicon::after{
    content: '';
    position: absolute;
    background-image: url('/assets/pics/line.svg');
    background-size: 192px;
    width: 190px;
    background-repeat: no-repeat;
    height: 182px;
    top: 325px;
    left: 162px;
  }
  .bigText{
    font-size: 3em;
  }
  .boxB{
    left: 100px !important;
  }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (max-width: 600px) {
  .flex-column-center{
    padding: 0 63px 0 0;
  }
  .title-text{
    z-index: 1;
    position: relative;
    margin: 219px 0;
    width: 75vw;
  }
}
@media only screen and (max-width: 452px) {
  .stage-box{
    left: 8% !important;
    top: -24px;
    width: 253px;
  }
  .home-padding{
    padding: 30px 30px;
  }
  .home-padding-e{
    padding: 30px 10px;
  }
  #yoopageicon::after{
    top: 325px ;
    left: 112px ;
  }
  .bigText{
    font-size: 2.2em !important;
    justify-content:none;
  }
  .title-text{
    z-index: 1;
    position: relative;
    margin: 219px 0;
    width: 85vw !important;
  }
}
/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (max-width: 768px) {
  .flex-column-center{
    padding: 0 103px 0 0;
  }
  .flex-column-center h1{
    font-size: 2.1em;
  }

}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (max-width: 992px) {
  .title-text{
    z-index: 1;
    position: relative;
    margin: 219px 0;
    width: 65vw;
  }
  #logo{
    display: none;
  }
  #logo2{
    display: inline-block;
    float: right;
  }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {

}


/* CSS */
.sys-btn, #theme > app-root > dashboard > div > div > div > a {
  align-items: center;
  background: linear-gradient(93deg, #D62486, #3F228C) !important;
  border: 2px solid transparent;
  border-radius: 100px;
  box-sizing: border-box;
  color: #ffffff;
  cursor: pointer;
  display: inline-flex;
  //font-family: "Segoe UI",  "Segoe UI Emoji", "Segoe UI Symbol", "Lucida Grande", Helvetica, Arial, sans-serif;
  font-size: 16px;
  font-weight: 600;
  justify-content: center;
  line-height: 20px;
  min-height: 40px;
  overflow: hidden;
  padding: 0 20px;
  text-align: center;
  touch-action: manipulation;
  //transition: background-color 0.167s cubic-bezier(0.4, 0, 0.2, 1) 0s, box-shadow 0.167s cubic-bezier(0.4, 0, 0.2, 1) 0s, color 0.167s cubic-bezier(0.4, 0, 0.2, 1) 0s;
  user-select: none;
  -webkit-user-select: none;
  vertical-align: middle;
}

.sys-btn3{
  align-items: center;
  border: 2px solid transparent;
  border-radius: 30px;
